.Ticked {
  width: 502px;
  height: 184px;
  box-sizing: border-box;
  border-radius: 5px;
  background: #fff;
  box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.1);
  margin: 0;
  padding: 20px;
  margin-bottom: 20px;
}

.Ticked__content {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  justify-content: space-between;
}

.Ticked__header-container {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
  justify-content: space-between;
  align-items: center;
}

.Ticked__content-container {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
  justify-content: space-between;
}

.Ticked__Route,
.Ticked__Lenght,
.Ticked__Stops {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: min-content;
  justify-content: space-between;
}
